import React from 'react'
import DashboardNavigation from '../DashboardNavigation/DashboardNavigation'
import CreateBlog from '../CreateBlog/CreateBlog'

function Dashboard() {
    return (
        <div className="dashboard">
            <DashboardNavigation />
            <div className="flex">
                {/* sidebar menu */}
                <div className="border-r border-gray-400 h-screen px-3 pt-3">
                    <ul>
                        <li>Dashboard</li>
                        <li>Blog</li>
                    </ul>
                </div>
                {/* action area */}
                <CreateBlog />
            </div>
        </div>
    )
}

export default Dashboard
