import React from "react";
import "./TipTap.css";
import { useEditor, EditorContent } from "@tiptap/react";
import StarterKit from "@tiptap/starter-kit";

import Heading from "@tiptap/extension-heading";
import Paragraph from "@tiptap/extension-paragraph";
// import Text from "@tiptap/extension-text";
// import Document from "@tiptap/extension-document";

// import TextAlign from "@tiptap/extension-text-align";
// import Highlight from "@tiptap/extension-highlight";

// import TaskList from "@tiptap/extension-task-list";
// import TaskItem from "@tiptap/extension-task-item";

// import CharacterCount from "@tiptap/extension-character-count";

import FormatBoldSharpIcon from "@material-ui/icons/FormatBoldSharp";
import FormatItalicSharpIcon from "@material-ui/icons/FormatItalicSharp";
import StrikethroughSSharpIcon from "@material-ui/icons/StrikethroughSSharp";
import CodeSharpIcon from "@material-ui/icons/CodeSharp";
import UndoSharpIcon from "@material-ui/icons/UndoSharp";
import RedoSharpIcon from "@material-ui/icons/RedoSharp";
import FormatListNumberedSharpIcon from "@material-ui/icons/FormatListNumberedSharp";
import FormatListBulletedSharpIcon from "@material-ui/icons/FormatListBulletedSharp";
import FormatQuoteSharpIcon from "@material-ui/icons/FormatQuoteSharp";

const MenuBar = ({ editor }) => {
  if (!editor) {
    return null;
  }
  return (
    <div className="w-full p-2 flex justify-evenly">
      <FormatBoldSharpIcon
        className="cursor-pointer bg-black text-white"
        onClick={() => editor.chain().focus().toggleBold().run()}
      />
      <FormatItalicSharpIcon
        className="cursor-pointer bg-black text-white"
        onClick={() => editor.chain().focus().toggleItalic().run()}
      />
      <StrikethroughSSharpIcon
        className="cursor-pointer"
        onClick={() => editor.chain().focus().toggleStrike().run()}
      />
      <CodeSharpIcon
        className="cursor-pointer"
        onClick={() => editor.chain().focus().toggleCode().run()}
      />
      <select className="border border-gray-400 rounded-md">
        <option onClick={() => editor.chain().focus().setParagraph().run()}>
          Paragraph
        </option>
        <option
          onClick={() =>
            editor.chain().focus().toggleHeading({ level: 1 }).run()
          }
        >
          H1
        </option>
        <option
          onClick={() =>
            editor.chain().focus().toggleHeading({ level: 2 }).run()
          }
        >
          H2
        </option>
        <option
          onClick={() =>
            editor.chain().focus().toggleHeading({ level: 3 }).run()
          }
        >
          H3
        </option>
        <option
          onClick={() =>
            editor.chain().focus().toggleHeading({ level: 4 }).run()
          }
        >
          H4
        </option>
        <option
          onClick={() =>
            editor.chain().focus().toggleHeading({ level: 5 }).run()
          }
        >
          H5
        </option>
        <option
          onClick={() =>
            editor.chain().focus().toggleHeading({ level: 6 }).run()
          }
        >
          H6
        </option>
      </select>
      <FormatListBulletedSharpIcon
        className="cursor-pointer"
        onClick={() => editor.chain().focus().toggleBulletList().run()}
      />
      <FormatListNumberedSharpIcon
        className="cursor-pointer"
        onClick={() => editor.chain().focus().toggleOrderedList().run()}
      />
      <CodeSharpIcon
        className="cursor-pointer"
        onClick={() => editor.chain().focus().toggleCodeBlock().run()}
      />
      <FormatQuoteSharpIcon
        className="cursor-pointer"
        onClick={() => editor.chain().focus().toggleBlockquote().run()}
      />
      <UndoSharpIcon
        className="cursor-pointer"
        onClick={() => editor.chain().focus().undo().run()}
      />
      <RedoSharpIcon
        className="cursor-pointer"
        onClick={() => editor.chain().focus().redo().run()}
      />
    </div>
  );
};

const Tiptap = (props) => {
  console.log("props :: ", props)
  const editor = useEditor({
    extensions: [
      StarterKit,
      Heading,
      Paragraph,
    ],
    content: props.content,
  });

  return (
    <div className="border-2 border-gray-700 rounded-lg">
      <MenuBar editor={editor} />
      <div className="w-full border-b-2 border-gray-700"></div>
      <EditorContent editor={editor}  className="p-2" />
    </div>
  );
};

export default Tiptap;
